body {
	font-family: 'Segoe UI', Arial, sans-serif;
	margin: 0;
	background: linear-gradient(135deg, #e8f5fd 0%, #f5f8fa 100%);
	color: #0f1419;
	min-height: 100vh;
	letter-spacing: 0.01em;
	-webkit-font-smoothing: antialiased;
	overflow-x: hidden;
}

::-webkit-scrollbar {
	width: 10px;
	background: #e6ecf0;
}
::-webkit-scrollbar-thumb {
	background: #b3d6f5;
	border-radius: 8px;
}

.navbar {
	background: #fff;
	border-bottom: 1.5px solid #d7e6f3;
	padding: 16px 0 16px 0;
	text-align: center;
	font-size: 2.1rem;
	font-weight: 800;
	letter-spacing: 2px;
	color: #1d9bf0;
	position: sticky;
	top: 0;
	z-index: 10;
	box-shadow: 0 2px 12px rgba(29,155,240,0.07);
	border-radius: 0 0 22px 22px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.app {
	display: flex;
	gap: 40px;
	padding: 40px 32px 0 32px;
	max-width: 1200px;
	margin: 0 auto;
}

@media (max-width: 900px) {
	.app {
		flex-direction: column;
		padding: 12px 4px 0 4px;
		gap: 0;
		max-width: 100vw;
	}
	.sidebar {
		width: 100%;
		min-height: unset;
		margin-bottom: 16px;
		border-radius: 12px;
		box-shadow: 0 1px 3px rgba(15,20,25,0.07);
		padding: 12px 8px;
	}
	main {
		padding: 0;
	}
}

@media (max-width: 600px) {
	.navbar {
		font-size: 1.1rem;
		padding: 8px 0;
	}
	.app {
		padding: 4px 0 0 0;
	}
	.sidebar {
		padding: 10px 4px;
		border-radius: 8px;
	}
	.profile-section {
		gap: 8px;
		margin-bottom: 16px;
	}
	.profile-avatar {
		width: 36px;
		height: 36px;
	}
	.card {
		padding: 8px;
		border-radius: 8px;
		margin-bottom: 10px;
	}
	.tweet {
		padding: 10px 8px 8px 8px;
		border-radius: 8px;
		margin-bottom: 8px;
	}
	.composer textarea {
		font-size: 0.95rem;
		height: 60px;
		padding: 6px;
	}
	.composer button {
		padding: 6px 12px;
		font-size: 0.95rem;
	}
}

.sidebar {
	width: 320px;
	background: #fff;
	border-radius: 22px;
	box-shadow: 0 4px 18px rgba(29,155,240,0.10);
	padding: 32px 22px;
	min-height: 420px;
	border: 1.5px solid #e6ecf0;
	display: flex;
	flex-direction: column;
	gap: 24px;
}

.profile-section {
	display: flex;
	align-items: center;
	gap: 18px;
	margin-bottom: 32px;
	padding-bottom: 18px;
	border-bottom: 1.5px solid #e6ecf0;
}

.profile-avatar {
	width: 60px;
	height: 60px;
	border-radius: 50%;
	background: #e6ecf0;
	object-fit: cover;
	border: 2.5px solid #1d9bf0;
	box-shadow: 0 4px 16px rgba(29,155,240,0.13);
}

main {
	flex: 1;
}

.card {
	background: #fff;
	padding: 28px 22px;
	border-radius: 18px;
	box-shadow: 0 4px 18px rgba(29,155,240,0.10);
	margin-bottom: 24px;
	border: 1.5px solid #e6ecf0;
	transition: box-shadow 0.2s, border 0.2s;
}
.card:focus-within, .card:hover {
	box-shadow: 0 8px 32px rgba(29,155,240,0.18);
	border: 1.5px solid #1d9bf0;
}

.composer textarea {
	width: 100%;
	height: 80px;
	padding: 12px;
	border-radius: 10px;
	border: 1.5px solid #e6ecf0;
	font-size: 1.05rem;
	resize: vertical;
	background: #f7fbfd;
	transition: border 0.2s;
}
.composer textarea:focus {
	border: 1.5px solid #1d9bf0;
	outline: none;
	background: #e8f5fd;
}

.composer button {
	margin-top: 10px;
	padding: 10px 28px;
	border-radius: 22px;
	border: none;
	background: linear-gradient(90deg, #1d9bf0 60%, #0c7abf 100%);
	color: #fff;
	cursor: pointer;
	font-weight: 700;
	font-size: 1.08rem;
	box-shadow: 0 2px 8px rgba(29,155,240,0.08);
	transition: background 0.2s, box-shadow 0.2s;
}
.composer button:hover, .composer button:focus {
	background: linear-gradient(90deg, #0c7abf 60%, #1d9bf0 100%);
	box-shadow: 0 4px 16px rgba(29,155,240,0.13);
}

.tweet {
	background: #fff;
	padding: 24px 22px 18px 22px;
	border-radius: 18px;
	margin-bottom: 18px;
	box-shadow: 0 2px 8px rgba(29,155,240,0.10);
	border: 1.5px solid #e6ecf0;
	transition: box-shadow 0.2s, border 0.2s;
	position: relative;
}
.tweet:hover, .tweet:focus-within {
	box-shadow: 0 10px 36px rgba(29,155,240,0.18);
	border: 1.5px solid #1d9bf0;
}
.tweet strong {
	font-size: 1.18rem;
	color: #1d9bf0;
	font-weight: 800;
}
.tweet p {
	margin: 12px 0 0 0;
	font-size: 1.08rem;
	line-height: 1.6;
}
.tweet audio, .tweet video {
	margin-top: 12px;
	width: 100%;
	border-radius: 12px;
	background: #f7fbfd;
}
.auth-form {
	max-width: 400px;
	margin: 64px auto 0 auto;
	background: linear-gradient(135deg, #fafdff 60%, #e8f5fd 100%);
	border-radius: 28px;
	box-shadow: 0 8px 32px rgba(29,155,240,0.18), 0 1.5px 8px rgba(29,155,240,0.07);
	padding: 48px 36px 36px 36px;
	border: 1.5px solid #e6ecf0;
	transition: box-shadow 0.2s, border 0.2s;
}
.auth-form:hover, .auth-form:focus-within {
	box-shadow: 0 12px 48px rgba(29,155,240,0.22), 0 2px 12px rgba(29,155,240,0.10);
	border: 1.5px solid #1d9bf0;
}
.auth-form input {
	width: 100%;
	padding: 12px 14px;
	margin-bottom: 16px;
	border-radius: 10px;
	border: 1.5px solid #e6ecf0;
	font-size: 1.05rem;
	background: #f7fbfd;
	transition: border 0.2s;
	box-sizing: border-box;
}

.password-input-wrapper {
	position: relative;
	margin-bottom: 16px;
}
.password-input {
	width: 100%;
	padding: 12px 44px 12px 14px;
	border-radius: 10px;
	border: 1.5px solid #e6ecf0;
	font-size: 1.05rem;
	background: #f7fbfd;
	transition: border 0.2s;
	box-sizing: border-box;
}
.password-input:focus {
	border: 1.5px solid #1d9bf0;
	outline: none;
	background: #e8f5fd;
}
.password-eye-btn {
	position: absolute;
	right: 10px;
	top: 50%;
	transform: translateY(-50%);
	background: none;
	border: none;
	cursor: pointer;
	padding: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 28px;
	width: 28px;
	z-index: 2;
}
.auth-form input:focus {
	border: 1.5px solid #1d9bf0;
	outline: none;
	background: #e8f5fd;
}
.auth-form button[type="submit"] {
	width: 100%;
	padding: 14px 0;
	border-radius: 28px;
	border: none;
	background: linear-gradient(90deg, #1d9bf0 60%, #0c7abf 100%);
	color: #fff;
	font-weight: 700;
	font-size: 1.15rem;
	margin-bottom: 12px;
	box-shadow: 0 3px 12px rgba(29,155,240,0.10);
	transition: background 0.2s, box-shadow 0.2s, transform 0.1s;
	cursor: pointer;
	letter-spacing: 0.03em;
}
.auth-form button[type="submit"]:hover, .auth-form button[type="submit"]:focus {
	background: linear-gradient(90deg, #0c7abf 60%, #1d9bf0 100%);
	box-shadow: 0 8px 24px rgba(29,155,240,0.18);
	transform: translateY(-2px) scale(1.03);
}
.auth-form button {
	font-size: 1.05rem;
}
.auth-form h2 {
	margin-bottom: 22px;
	font-size: 2.2rem;
	color: #1d9bf0;
	font-weight: 900;
	letter-spacing: 2.5px;
}
